$main: #6a1051;

//restaurant profile
.rest-profile-wrapper {
    text-align: center;
    margin-bottom: 1rem;
    padding: 2rem 0rem;
    .avatar {
        width: 200px;
        height: 200px;
        margin: 0 auto 2rem; }
    .rest-name {
        text-transform: capitalize;
        font: {
            weight: 300;
            size: 3rem; }
        margin-bottom: 0.8rem; }
    .location {
        font-size: 1.2rem;
        text-transform: capitalize; } }

.rest-wrapper {
    display: flex;
    flex-wrap: wrap;
    .rest-unit {
        width: 24%;
        margin: 1% 0.5%;
        text-align: center;
        padding: 4rem 0rem;
        border: 1px solid rgba(black, 0.2 );
        border-radius: 5px;
        .avatar {
            width: 80px;
            height: 80px;
            background: {
                size: cover;
                position: center; }
            border-radius: 50%;
            margin: 0 auto 1rem; } } }
.restaurant .cart {
    position: fixed;
    width: 300px;
    top: 100px;
    right: 40px;
    background: white;
    padding: 1rem;
    box-shadow: 5px 5px 10px rgba(black, 0.4); }

.error-container {
    position: fixed;
    left: 50%;
    top: 50%;
    background: white;
    padding: 5rem 2rem;
    width: 500px;
    transform: translate(-50%, -50%);
    box-shadow: 5px 5px 10px rgba(black, 0.4);
    .error {
        font-size: 2rem;
        text-transform: capitalize; }
    .inner {
        position: relative; } }


@media(max-width: 750px) {
    .rest-wrapper {
        .rest-unit {
            width: 49%; } } }


//small screen
@media(max-width: 400px) {
    .rest-wrapper {
        .rest-unit {
            width: 100%; } } }
