* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

h1, h2, h3, h4, h5, p {
  margin: 0;
  padding: 0; }

html, body {
  font-family: 'Fira Sans', sans-serif; }

body {
  padding-bottom: 8rem; }

a {
  color: black;
  text-decoration: none; }

ul {
  list-style-type: none;
  margin: 0;
  padding: 0; }

.layout {
  display: flex;
  width: 800px;
  max-width: 90%;
  margin: 0 auto;
  justify-content: space-between; }
  .layout .menu {
    width: 70%;
    margin-right: 2rem; }

.container {
  width: 1100px;
  max-width: 100%;
  margin: 0 auto;
  padding: 2rem 1rem 0rem 1rem; }

.form-wrapper {
  width: 450px;
  max-width: 100%;
  padding: 0rem 2rem; }

.form-center {
  margin: 0 auto; }
  .form-center .form-element {
    margin-bottom: 1rem; }

.main-title {
  font-size: 2.4rem;
  font-weight: 300;
  margin-bottom: 1rem;
  text-transform: capitalize; }
  .main-title span {
    font-weight: 400;
    color: #6a1051; }

.text-center {
  text-align: center; }

.btn {
  display: block;
  width: 100%;
  padding: 0.9rem 0rem;
  outline: none;
  border: none;
  cursor: pointer; }

.btn-main {
  background: #6a1051;
  color: white; }

.btn-danger {
  background: red;
  color: white; }

.btn-success {
  background: green;
  color: white; }

.error {
  color: red;
  font-size: 1rem;
  margin-bottom: 0.8rem;
  text-align: center; }

.loading {
  background: green;
  text-align: center;
  padding: 0.8rem 0rem;
  color: white;
  font-size: 1.2rem; }

.header-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 2rem; }
  .header-wrapper .cta {
    display: block;
    background: #382039;
    color: white;
    padding: 1rem;
    text-align: center; }

.user-unit-wrapper {
  display: flex;
  flex-wrap: wrap; }
  .user-unit-wrapper .user-unit {
    width: 24%;
    margin: 1% 0.5%;
    display: inline-block;
    padding: 2rem 0rem;
    text-align: center;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 5px; }
    .user-unit-wrapper .user-unit .face {
      width: 120px;
      height: 120px;
      background-size: cover;
      background-position: center;
      margin: 0 auto 1rem;
      border-radius: 50%; }
    .user-unit-wrapper .user-unit .content p {
      margin-bottom: 0.5rem;
      font-size: 1.1rem; }

.form-wrapper {
  width: 540px;
  max-width: 90%;
  margin: 0 auto; }
  .form-wrapper button {
    display: inline-block;
    padding: 1rem 0rem;
    width: 100%;
    background: #382039;
    color: white;
    outline: none;
    border: none; }
  .form-wrapper .form-element {
    margin-bottom: 1rem; }
    .form-wrapper .form-element label {
      display: block;
      margin-bottom: 1rem; }
    .form-wrapper .form-element input, .form-wrapper .form-element select {
      width: 100%;
      padding: 0.8rem 0rem;
      text-indent: 1rem;
      font-size: 1.1rem; }

.error {
  font-size: 1rem;
  font-weight: 400;
  color: red;
  text-align: center; }

.info {
  color: green;
  font-size: 1.2rem;
  margin-bottom: 1rem;
  display: block; }

.feedback {
  font-size: 1.2rem; }

.text-center {
  text-align: center; }

@media (max-width: 840px) {
  .header-wrapper {
    display: block; }
  .user-unit-wrapper .user-unit {
    width: 49%; } }

@media (max-width: 400px) {
  .user-unit-wrapper .user-unit {
    width: 70%;
    margin: 0 auto 2rem; } }
