.main-header {
    margin-bottom: 2rem;
    .container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        nav a {
            display: inline-block;
            margin-left: 1rem;
            font-size: 0.9rem; }
        .menu {
            font-size: 1.8rem;
            display: none;
            cursor: pointer; } }

    nav {
        display: flex; } }

.links-wrapper {
    display: flex;
    li a {
        margin-left: 1rem; } }
@media(max-width: 400px) {
    .main-header .container {
        nav {
            display: none; }
        .menu {
            display: block; } } }
